.todoItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 10px;
  padding: 4px 12px;
  margin: 12px;
  border: 2px solid #fec5bb;
  border-radius: 15px;
  box-shadow: 0px 0px 4px 2px rgba(10, 0, 10, 0.2);
  font-weight: 300;
}
.todoItem-p {
  overflow-wrap: break-word;
  hyphens: auto;
  inline-size: 75%;
  text-align: center;
  text-justify: auto;
  line-height: 1.3;
}
.icon {
  font-size: 22px;
}
.complete-btn:hover {
  transition: color 0.2s ease;
  color: #d0efb1;
  cursor: pointer;
}

.delete-btn:hover {
  transition: color 0.2s ease;
  color: #ff5964;
  cursor: pointer;
}

.todoItem-c--completed {
  color: #d0efb1;
}

.todoItem-p--completed {
  text-decoration: line-through;
}
